"use client";
import { useState } from "react";

const categories = [
    {
        title: "Nội thất ô tô",
        subcategories: ["Ghế trẻ em", "Thảm trải sàn ô tô"],
    },
    {
        title: "Ngoại thất ô tô",
        subcategories: ["Cản trước", "Cản sau"],
    },
    {
        title: "Phụ kiện công nghệ",
        subcategories: ["Camera hành trình", "Sạc điện thoại"],
    },
    {
        title: "Chăm sóc xe",
        subcategories: ["Dung dịch rửa xe", "Khăn lau xe"],
    },
    {
        title: "An toàn và bảo vệ",
        subcategories: ["Bình chữa cháy", "Cảm biến áp suất lốp"],
    },
];

export default function CategoryMenu() {
    const [activeIndex, setActiveIndex] = useState(null);

    return (
        <div className="flex w-full max-w-4xl border shadow-md">
            {/* Danh mục chính */}
            <div className="w-1/3 bg-gray-50">
                {categories.map((category, index) => (
                    <div
                        key={index}
                        className={`px-4 py-3 cursor-pointer hover:bg-gray-200 flex justify-between items-center ${
                            activeIndex === index ? "bg-gray-200" : ""
                        }`}
                        onMouseEnter={() => setActiveIndex(index)}
                        onMouseLeave={() => setActiveIndex(null)}
                    >
                        <span>{category.title}</span>
                        <span>›</span>
                    </div>
                ))}
            </div>

            {/* Danh mục con */}
            <div className="w-2/3 bg-white p-4">
                {activeIndex !== null &&
                    categories[activeIndex].subcategories.map((sub, i) => (
                        <div key={i} className="py-1 px-2 hover:underline cursor-pointer">
                            {sub}
                        </div>
                    ))}
            </div>
        </div>
    );
}
